import PropTypes from 'prop-types';
import React, {Component} from 'react';

/**
 * ExampleComponent is an example component.
 * It takes a property, `label`, and
 * displays it.
 * It renders an input with the property `value`
 * which is editable by the user.
 */
export default class LoadingWrapper extends Component {
    constructor(props) {
        super(props);
        this.ref = React.createRef();
        this.timestamp = 0;
        this.isLoading = false;
    }
    componentDidMount() {
        let mo = new MutationObserver((mutations) => {
            for (const mutation of mutations) {
                if (this.isSpinner(mutation.addedNodes)) {
                    this.setLoading(true);
                    break;
                }
                if (this.isSpinner(mutation.removedNodes)) {
                    this.setLoading(false);
                    break;
                }
            }
        });
        mo.observe(this.ref.current, {
            childList: true,
            attributes: true,
            characterData: true,
            subtree: true,
        });
    }
    isSpinner(nodes) {
        for (const node of nodes) {
            if (!node.tagName) continue;
            if (!node.className) continue;
            if (
                node.className
                    .split(' ')
                    .indexOf(this.props.targetClassName) !== -1
            ) {
                return true;
            }
        }
        return false;
    }
    setLoading(isLoading) {
        if (this.isLoading !== isLoading) {
            if (!isLoading) {
                this.timestamp = Date.now();
            }
            this.props.setProps({is_loading: isLoading});
            this.isLoading = isLoading;
        }
    }
    render() {
        const {id, className, children} = this.props;
        return (
            <div
                id={id}
                className={'loading_wrapper ' + className}
                ref={this.ref}
            >
                {children}
            </div>
        );
    }
}

LoadingWrapper.defaultProps = {};

LoadingWrapper.propTypes = {
    children: PropTypes.node,
    id: PropTypes.string,
    className: PropTypes.string,
    targetClassName: PropTypes.string.isRequired,
    setProps: PropTypes.func,
};
